        explanation: `<p><strong>Kin thc:</strong> c hiu</p><p><strong>Gii thch: </strong>T no sau y ng ngha vi <strong><u>cottages</u></strong>?</p><p>A. churches (n): nh th        B. shops (n): ca hng</p><p>C. grass (n): c               D. houses (n): nh</p><p><strong>Thng tin:</strong> The village green is a wide stretch of grass, and houses or <strong>cottages </strong>are built round it.<strong></strong></p><p><strong>Tm dch:</strong> Mu xanh ca lng l mt bi c rng, v nhng ngi nh hoc ngi nh tranh c xy dng xung quanh n.</p><p><strong>Chn D.</strong></p>`,
