        explanation: `<p><strong>Kin thc:</strong> c hiu</p><p><strong>Gii thch: </strong>c im chung ca lng Anh l g?</p><p>A. Chng c nc chy.</p><p>B. Chng c mt nh th.</p><p>C. Chng c mt ngi lng xanh.</p><p>D. Chng c nh th vi mt ngn thp cao, v mt ngi lng xanh ti.</p><p><strong>Thng tin:</strong> Almost every village has a church, the round or square tower if which can be seen from many miles around.<strong></strong></p><p><strong>Tm dch:</strong> Hu ht cc lng c mt nh th, vng hoc thp vung nu c th c nhn thy t nhiu dm xung quanh.</p><p><strong>Chn B.</strong></p>`,
